import { getProgressDetail } from "~/api/agent";
export default {
  data() {
    return {
      id: "", // 扶农
      content: {
        content: "", // 内容
        main_title: "", // 标题
        send_date: '', // 发送时间
      },
    };
  },
  mounted() {
    if (process.client) {
      document.title = "扶农进展";
    }
    this.id = this.$route.query.id;
    this.getData();
    // 获取所有的省份
  },

  methods: {
    getData() {

      getProgressDetail({
        id: this.id,
      }).then((res) => {
        if (res.data.code === 1000) {
          this.content = res.data.data;
          this.text = res.data.data;
           if (process.client) {
              document.title =  res.data.data.main_title;
            }
        } else {
          alert(res.data.message);
        }
      });
    },
  },
};
